export default {
	data() {
		return {
			title:'',
			// titles:'',
			strings:''
		};
	},
	
	onLoad:function(e){
		//console.log(JSON.stringify(e));
		//this.titles= e.title
		uni.setNavigationBarTitle({
			title:"res.data.title"
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				this.title =res.data.title;
				uni.setNavigationBarTitle({
					title:res.data.title
				});
				this.strings =res.data.content;
			},
			fail: () => {},
			complete: () => {}
		});		
	}
}
